import React from 'react';
import { Image } from 'react-bootstrap';
import Wobble from 'react-reveal/Wobble';
import './App.css';

const Footer = () => { 

  return (
    <div className="footer">  
      <Wobble>    
      <br />
      {/* <span>                      */}
        <div align="center">
          <Image src="/images/react.jpg" thumbnail alt="React" height="75" width="75" className="footerimage"></Image>
          {/* <p className="legend">React</p> */}
          <Image src="/images/redux.png" thumbnail alt="Redux" height="75" width="75" className="footerimage"></Image>
          {/* <p className="legend">Redux</p> */}
          <Image src="/images/html.png" thumbnail alt="HTML" height="75" width="75" className="footerimage"></Image>
          {/* <p className="legend">HTML</p> */}
          <Image src="/images/javascript.jpg" thumbnail alt="Javascript" height="75" width="75" className="footerimage"></Image>
          {/* <p className="legend">Javascript</p> */}
          <Image src="/images/nodejs.jpg" thumbnail alt="Nodejs" height="75" width="75" className="footerimage"></Image>
          {/* <p className="legend">Typescript</p> */}
        </div>                        
      {/* </span>       */}
      <br />      
      <em>Project Portfolio, © Abhay Desai 2020</em>     
      </Wobble> 
    </div>
  )
}

export default Footer;